* {
  border: 0;
  padding: 0;
  margin: 5px 5px;
  box-sizing: border-box;
}

.grid {
  display: grid;
  grid-template-columns: repeat(5, 20%);
  grid-template-rows: repeat(8, minmax(100px, auto));
  height: 98vh;
}

.header {
  background-color: rgb(27, 255, 27);
  color: rgb(30, 34, 255);
  font-size: 64px;
  text-align: center;
  grid-column: 1 / 6;
  grid-row: 1 / 2;
}

.sideNavLeft {
  background-color: rgb(255, 0, 255);
  color: white;
  font-size: 64px;
  grid-column: 1 / 2;
  grid-row: 2 / 8;
}

.main {
  background-color: yellow;
  color: rgb(0, 0, 0);
  font-size: 64px;
  grid-column: 2 / 5;
  grid-row: 2 / 8;
}

.sideNavRight {
  background-color: rgb(255, 0, 255);
  color: white;
  font-size: 64px;
  grid-column: 5 / 6;
  grid-row: 2 / 8;
}

.footer {
  background-color: rgb(27, 255, 27);
  color: rgb(30, 34, 255);
  font-size: 64px;
  grid-column: 1 / 6;
  grid-row: 8 / 9;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}